import { maxMin } from './config';

class Helper extends Data {
  between() {
    return (
      Math.floor(Math.random() * (tempArr.maxMin[0] - tempArr.maxMin[1] + 1)) +
      tempArr.maxMin[1]
    );
  }

  randomTwoNums() {
    if (arr.length < tempArr.totalNums) {
      const num = this.between();
      if (!arr.includes(num)) arr.push(num);
      this.randomTwoNums();
    }
  }
}

export default new Helper();
